<template>
  <div class="bs-user">
    <el-table :data="userList" style="width: 100%" max-height="250">
      <el-table-column fixed prop="add_time" label="创建日期" width="200"></el-table-column>
      <el-table-column prop="username" label="姓名" width="150"></el-table-column>
      <el-table-column property="disable" label="是否封禁" width="200"></el-table-column>
      <el-table-column prop="scores" label="现有积分" width="120"></el-table-column>
      <el-table-column prop="email" label="邮箱" width="400"></el-table-column>
      <el-table-column fixed="right" label="操作" width="380">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="deleteRow(scope.$index, userList)"
            type="text"
            size="small"
          >移除</el-button>
          <el-button
            @click.native.prevent="deleteRow(scope.$index, userList)"
            type="text"
            size="small"
          >查看</el-button>
          <el-button
            @click.native.prevent="modifyData(scope.$index, userList)"
            type="text"
            size="small"
          >修改</el-button>
        </template>
      </el-table-column>
    </el-table>
    <userModal v-if="isUserModal" :userData='selectUserDate' @close='closeUserModal'></userModal>
  </div>
</template>

<script>
import * as ajax from "@/api";

export default {
  created() {
    ajax.getUserList().then(resp => {
      console.log(resp)
      if (resp.data.code) {
        resp.data.data.user_list.forEach(element => {
          element['is_disable'] ? element['disable']='是' : element['disable']='否'
        });
        this.userList = resp.data.data.user_list;
        console.log(resp.data.data.user_list)
      }
    });
  },
  data(){
      return{
          userList:[],
          selectUserDate:'',
          isUserModal:false
      }
  },
  methods:{
    modifyData(index,list){
      this.isUserModal = true
      this.selectUserDate = list[index]
    },
    closeUserModal(){
      this.isUserModal = false
    }
  },
  components:{
    userModal : resolve => require(['@/components/User/userModal'],resolve)
  }
};
</script>

<style lang='scss'>
.bs-user {
  flex: 1;
  .el-table {
    .cell {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .el-table__body-wrapper {
      overflow-y: hidden;
    }
    max-height: 100% !important;
    .el-table__body-wrapper {
      max-height: 100% !important;
    }
  }
  .el-table__fixed-body-wrapper{
    max-height: 100% !important;
  }
}
</style>